import * as React from 'react';

export default function calendar(attrs: Record<string, any>) {
  const { metadata } = attrs;
  const { time, place, comment, subject, topReceivers = [] } = metadata;

  return (
    <div>
      <div>{subject}</div>
      {time ? <div>{time}</div> : null}
      {place ? <div>{place}</div> : null}
      <div>
        {topReceivers.map(rec => <span key={rec.uid} style={{ marginRight: '4px' }}>{rec.name || ''} </span>)}
      </div>
      {comment ? <div>{comment}</div> : null}
    </div>
  );
}
